@font-face {
  font-family: "AvenirNextLTPro";
  src: url("./Fonts/AvenirNextLTPro-Regular.otf") format("otf"),
    url("./Fonts/AvenirNextLTPro-It.otf") format("otf"),
    url("./Fonts/AvenirNextLTPro-DemiCn.otf") format("otf"),
    url("./Fonts/AvenirNextLTPro-Demi.otf") format("otf"),
    url("./Fonts/AvenirNextLTPro-Bold.otf") format("otf"),
    url("./Fonts/AvenirNextLTPro-BoldCn.otf") format("otf");
}
body {
  font-family: "Poppins", sans-serif;
  // font-family: "Avenirnextltpro demi", sans-serif;
  background-color: black;
  color: white;
}
.align_center {
  max-width: 1300px;
  margin: auto;
}
.blue {
  color: #0fdfc8 !important;
}
.head_sec {
  margin-top: 1.9rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  .logo_sec {
    h1 {
      margin: 0;
      letter-spacing: 2px;
      font-size: 3.5em;
      font-weight: 700;
      span{
        color: #0fdfc8;
      }
    }
  }
  .switchs {
    display: none;
  }
  .nav_sec {
    a {
      text-decoration: none;
      color: white;
      padding-left: 3.2rem;
      font-family: "Poppins", sans-serif;
      font-weight: 500;
      font-size: 21px;
      transition: 320ms;
    }
    :hover {
      color: #0fdfc8;
    }
  }
}
// section one

.hero_sec {
  margin-top: 5rem;
  display: flex;
  justify-content: space-between;
  color: white;

  .btn_align {
    padding: 11px 22px !important;
    margin-top: 0.5rem;
    text-transform: capitalize;
    color: var(--white);
    display: inline-block;
    font-family: po;
    font-weight: 600;
    font-size: 20px;
    line-height: 29px;
    min-width: 200px;
    text-align: center;
    font-family: "Poppins", sans-serif;
    border:1px solid #0fdfc8;
    .arrow_icon {
      margin-bottom: 2px;
      padding-left: 4px;
      font-size: 25px;
      color: #0fdfc8;
    }
  }
  .hero_text {
    max-width: 597px;
    margin: auto;
    .btnes {
      margin-top: 2rem;

      a{
        text-decoration: none;
      }
    }
    h1 {
      font-size: 65px;
      border-left: 9px solid #0fdfc8;
      padding-left: 2rem;
      span {
        color: #0fdfc8;
        box-shadow: inset 0 -58px 0 0 rgba(26, 187, 169, 0.1);
      }
    }
  }
}

// SECTION TWO

.Work_sec {
  margin: 5rem 0;


  .workSec_first h1 {
    color: white;
    letter-spacing: 2px;
    font-size: 3.5em;
    font-weight: 700;
    span {
      color: #0fdfc8;
    }
  }

  .workSec_second {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    margin: 4.9rem 0;
    .img_aligns {
      width: 400px;
      img {
        width: 100%;
      }
    }
    .v3 {
      width: 362px;
      height: 242px;
      padding-left: 1.2rem;
      img {
        width: 100%;
        height: 100%;
      }
    }
  }
  .button_work {
    display: flex;
    justify-content: center;
    margin-top: 2rem;
    a{
      text-decoration: none;
    }
    .btn_work {
      padding: 10px 28px !important;
      margin-top: 0.5rem;
      text-transform: capitalize;
      color: var(--white);
      display: inline-block;
      font-family: po;
      font-weight: 600;
      font-size: 20px;
      line-height: 29px;
      min-width: 200px;
      text-align: center;
      font-family: "Poppins", sans-serif;
      border: 1px solid #0fdfc8;
    }
    .btn_workinner {
      color: #0fdfc8;
      font-size: 25px;
      margin-bottom: 3px;
    }
  }
}

// Section Three
.Service_sec {
  margin-top: 10rem;
  .Service_Inner {
    h1 {
      color: white;
      letter-spacing: 2px;
      font-size: 3.5em;
      font-weight: 700;

      span {
        color: #0fdfc8;
      }
    }
  }
  .service_first {
  
    h1 {
      font-size: 42px;
      line-height: 55px;
      margin: 0 0 29px;
      max-width: 655px;
      margin-top: 3.9rem;
      span {
        color: #0fdfc8;
      }
    }
    p {
      max-width: 423px;
      font-size: 17px;
      font-weight: 100;
      line-height: 22px;
    }
  }
  .btn_service {
    padding: 13px 32px !important;
    margin-top: 1.5rem;
    text-transform: capitalize;
    color: var(--white);
    display: inline-block;
    font-family: po;
    font-weight: 600;
    font-size: 22px;
    text-decoration: none;
    line-height: 29px;
    min-width: 200px;
    text-align: center;
    font-family: "Poppins", sans-serif;
    border:1px solid #0fdfc8;
  }
  .service_boxes {
    margin: 5rem 0;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    align-items: center;

    .box_ {
      width: 326px;
      background: #000000;
      border: 1px solid #0fdfc8;
      color: white;
      padding: 30px 35px;
      border-radius: 6px;
      margin: 0 1rem;
      margin-bottom: 4rem;
      h4 {
        margin-bottom: 17px;
      }

      ul {
        margin: 0;
      }
    }
  }
}

// form section
.form_sec {
  background-color: white;
  padding: 2rem 0;


  .inner_form {
    max-width: 1300px;
    margin: auto;
    padding: 25px 35px;
    background-color: white;
    h1{
      text-align: center;
    }
    .main_form {
      margin-top: 5rem;
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      align-items: flex-start;
      color: black;
      .Fdetails_aside {
        flex-basis: 40%;

        .faside_image{
          max-width: 500px;
          margin: auto;
        img{
          width: 100%;
        }
        }
        h2 {
          font-size: 32px;
          line-height: 42px;
          font-weight: 600;
          margin: 0 0 8px;
        }
        p {
          font-size: 20px;
          line-height: 35px;
          margin: 0 0 56px;
        }
      }
      .form_aside {
        flex-basis: 45%;
        p {
          font-size: 22px;
          font-family: "Poppins", sans-serif;
          margin: 25px 14px 19px 9px;
        }
        .widths_form {
          width: 520px;
        }
        .form_btn{
          padding: 13px 32px !important;
          margin-top: 1.5rem;
          text-transform: capitalize;
          color: var(--white);
          display: inline-block;
          font-family: po;
          font-weight: 600;
          font-size: 22px;
          line-height: 29px;
          min-width: 200px;
          text-align: center;
          font-family: "Poppins", sans-serif;
          border-color: black;
        }
      }
    }
    h1 {
      color: black;
      letter-spacing: 2px;
      font-size: 3.5em;
      font-weight: 700;

      span {
        color: #0fdfc8;
      }
    }
  }
}

// footer section

.Footer_Section{
padding: 1rem 1rem 0rem 1rem;
  background-color: black;
display: flex;
flex-wrap: wrap;
justify-content: space-between;
align-items: center;
  h1{
    letter-spacing: 2px;
    font-size: 3.5em;
    font-weight: 700;
    margin-bottom: 0;
    span{
color: #0fdfc8;
    }
  }
  .footer_text{
padding: 1rem;
    p{
      margin-bottom: 0;
    }
  }
  .social_icons{
    padding: 1rem;
    .icons_footer{
      ul{
        padding: 0;
        text-decoration: none;
display: flex;
        li{
        list-style: none;
        padding:0 .2rem;
        a{
          color: white;
        }
        }
      }
    }
  }
}












@media screen and (max-width: 1484px) and (min-width: 1000px) {
  .align_center {
    max-width: 1000px;
  }
  .hero_sec {
    max-width: 1000px;
    margin: 5rem auto;
  }
  .hero_sec .hero_text h1 {
    font-size: 65px;
  }
  .video_sec video {
    width: 500px;
  }
}

@media screen and (max-width: 1000px) {
  .video_sec video {
    position: fixed;
    top: 10%;
    left: 30%;
    z-index: -1;
  }
  .Work_sec{
    padding: 0 2rem;
  }
.Fdetails_aside{
  flex-basis: 100%!important;
}
  .Service_sec{
    padding: 0 2rem;
  }
  .form_sec .inner_form .main_form{
    justify-content: center!important;
  }
  .head_sec {
    padding: 0 22px;
  }
}
@media screen and (max-width: 794px) {
  .nav_sec {
    display: none;
  }
  .switchs {
    display: block !important;
  }
  .hero_text {
    margin-left: 1rem !important;
  }
  .head_sec {
    padding: 0;
    margin-top: 0.5rem;
  }
 .widths_form{
  max-width: 400px;
}
  .logo_sec {
    padding-left: 14px;
  }
}
@media screen and (max-width: 540px) {
  .hero_sec .hero_text h1 {
    font-size: 55px;
    padding-left: 1.2rem;
  }
  .form_sec .inner_form .main_form .form_aside p{
    font-size: 14px;
    margin: 20px 15px 17px 11px;
  }

  .form_sec .inner_form .main_form .form_aside .form_btn{
    padding: 6px 6px !important;
    font-size: 16px;
  }
  .Footer_Section{
    flex-direction: column;
  }
  .Service_sec {
    margin-top: 0rem;
  }
}

@media screen and (max-width: 448px) {
  .hero_sec .hero_text h1 {
    font-size: 50px;
  }
  .widths_form{
    width:250px!important;
  }
  .head_sec .logo_sec h1{
    font-size: 2.5rem;
  }
}
@media screen and (max-width: 400px) {
  .hero_sec .hero_text h1 {
    font-size: 44px;
  }
  
  .Service_sec{
    padding: 1.5rem;
  }
  .Work_sec{
    padding: 0 1rem;
  }
  .Work_sec .workSec_second{
    margin: 1.9rem 0;
  }
}

@media screen and (max-width: 358px) {
  .hero_sec .hero_text h1 {
    font-size: 39px;
  }
 
  .Service_sec{
    margin-top: 5rem;
  }
  .Service_sec .service_boxes .box_{
    margin: 0 0rem;
    margin-bottom: 4rem;
  }
}

@media screen and (max-width: 324px) {
  .hero_sec .hero_text h1 {
    font-size: 35px;
  }
  .Work_sec .button_work .btn_work{
    padding: 10px 14px !important;
    font-size: 15px;
  }
  .Service_sec .Service_Inner h1{
    font-size: 2.5rem;
  }
  .Service_sec .service_first h1{
    font-size: 30px;
    line-height: 35px;
    margin: 0 0 25px;
    margin-top: 2.9rem;
  }
  .Service_sec .btn_service{
    padding: 8px 17px !important;
    font-size: 18px;
    
  }
}
@media screen and (max-width: 324px) {
  .hero_sec .hero_text h1 {
    font-size: 2.5vh;
  }
}
